import { Injectable } from '@angular/core';

@Injectable()
export class CheckinternetService {
  
  constructor() { }
  checkInternet(){    
    var networkState = navigator['connection'].type;
    console.log(networkState);
    if(networkState == "none"){
      return false;      
    }
    else{
      return true;
    }
  }
}



