import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'main',
  pure: true
})
export class MainPipe implements PipeTransform {
  
  transform(items: Array<any>,pipetype, item: Array<any>){
          //alert(pipetype);
    switch (pipetype) {
      case "fav":
        item = items.filter(checkFav);
        return item;
      case "nature":
        item = items.filter(item => item.category === "NAT");
        return item;
      case "abstract":
        item = items.filter(item => item.category === "ABS");
        return item;
      case "people":
        item = items.filter(item => item.category === "PEO");
        return item;
      case "places":
        item = items.filter(item => item.category === "PLA");
        return item;
      case "misc":
        item = items.filter(item => item.category === "MIS");
        return item;
      default: 
        return items;
    }


    /*favorites*/
    function checkFav(wallpaper){    
      var storedId = JSON.parse(localStorage.getItem("favorites"));    
      if(storedId.indexOf(wallpaper.category+wallpaper.id) > -1){
        return wallpaper;
      }
    }

  }

}



