import { Injectable, NgZone } from '@angular/core';
import { ShowpopupService } from './../services/showpopup.service';
import $ from 'jquery';
import lazyload from 'jquery-lazyload'
declare var jQuery, cordova; 

@Injectable()
export class GlobalsService {
  pipetype: string = "";
  mainwalls:boolean = true;
  downloadedwalls:boolean = false;
  showFeatured:boolean = true;
  showNavbar:boolean = true;
  popshow: Boolean = false; 
  home:boolean = true;
  filterApplied:boolean = false;
  downloadOverlayShow:boolean = false;
  Imgpath:string;
  thumbpath:string;
  wallDownloadMethod;
  downloadsCount:number = 0;
  favoritesCount:number = 0;
  noInternetTimeout:any
  downloadPath:any;
  androidOsVer:any

  /*home button to reset the wallpaper filter and return home*/ 
  resetPipe(){
    this._ngZone.run(() => {
      this.downloadedwalls = false;
      this.filterApplied = false;
      this.home = true; 
      this.pipetype = "";
      this.showFeatured = true;      
      this.mainwalls = true;
      this.showNavbar = true;
     })
    $("img.lazy").hide();   
    setTimeout(()=>{ this.lazyLoadStart();$("img.lazy").show(); },500);
    setTimeout(function(){
        $(".homeBtn").removeClass("animateShow").addClass("animateHide");
    },100);    
    this.showpopup.showLoader();
    setTimeout(()=>{this.showpopup.hideLoader()},1000);        
  }

  lazyLoadStart(){    
    (function($) {
      $("img.lazy").lazyload({
        effect : "fadeIn",
        threshold : 200
      });
    })(jQuery);
  }

  checkOSversion(){
    var ua = navigator.userAgent;
    if( ua.indexOf("Android") >= 0 )
    {
      var androidversion = parseFloat(ua.slice(ua.indexOf("Android")+8)); 
      if (androidversion < 6)
      {
          console.log(androidversion);
          this.androidOsVer = androidversion;
          this.downloadPath = "file:///storage/sdcard/wallpic";
      }
        else{
          this.downloadPath = "/storage/emulated/0/wallpic";
        }
    }
  }

  cancelDownload(){    
    this._ngZone.run(() => {
      this.downloadOverlayShow == false; 
    })            
    this.wallDownloadMethod.abort(); 
    setTimeout(function(){$("#downProShow").hide();},200); 
    window['plugins'].toast.showShortBottom('Download canceled.');
    setTimeout(()=>{console.log("Download overlay flag is: "+this.downloadOverlayShow) },100);      
  }

  resetDownPerc(){
    $("#downProShow .digits").html(0);
    $(".progressBar span").width(0);
    $("#downProShow .text").html("Starting download");
  }

  /*refresh count of downloads and favorites*/
  refreshDownAndFavCount(){
    if(localStorage.getItem("downloads") != null){
      this.downloadsCount = JSON.parse(localStorage.getItem("downloads")).length;
    }
    else{
      this.downloadsCount = 0;
    }
    if(localStorage.getItem("favorites") !=null){
      this.favoritesCount = JSON.parse(localStorage.getItem("favorites")).length;
    }
    else{
      this.favoritesCount = 0;
    }    
  }
  

  /*check write permission*/
  checkWritePerm(){
    console.log("Checking write permission.");
    var permissions = cordova.plugins.permissions;
    permissions.hasPermission(permissions.WRITE_EXTERNAL_STORAGE, function( status ){
      function error() {
        console.warn('Write permission is not turned on');
      }
      function success( status ) {
        if( !status.hasPermission ) error();
      }
      if (status.hasPermission) {
        return true;          
      }
      else {
        permissions.requestPermission(permissions.WRITE_EXTERNAL_STORAGE, success, error);          
      }
    });
  }

  constructor(public showpopup: ShowpopupService,private _ngZone: NgZone) { }

}




