import { Injectable } from '@angular/core';
import $ from 'jquery';

@Injectable()
export class ShowpopupService {
  popupVisible:boolean;
  popup2Visible:boolean;
  popupHeader:string;
  popupMessage:string;
  popupButton:string;
  popupButton2:string;
  reload:boolean = false;
  
  showNoInternetPopup(){    
    var header = "No internet!";
    var message = "Please check internet connection.";
    var button = "Try again";
    this.reload = true;
    if(!this.popupVisible){this.showPopup(header,message,button);}    
  }

  showConnErrorPopup(){
    var header = "Error!";
    var message = "Error occured while connecting to server.";
    var button = "Try again";
    this.reload = true;
    if(!this.popupVisible){this.showPopup(header,message,button);}  
  }

  showSubmitWallPopup(){
    var header = "Submit Wallpic";
    var message = "You can send picture taken by you to us on wallpic@gmail.com. We will notify you once it is available in the app.";
    var button1 = "Later";
    var button2 = "Send now";
    this.reload = false;
    if(!this.popupVisible){this.showPopup2(header,message,button1,button2);}  
  }

  showPopup(head,msg,btn){
    this.popupVisible = true;
    setTimeout(()=>{
    this.popupHeader = head;
    this.popupMessage = msg;
    this.popupButton = btn;
    $("#popup .popup").addClass("animfadeTopIn");
    },100);
  }
  showPopup2(head,msg,btn,btn2){
    this.popup2Visible = true;
    setTimeout(()=>{
    this.popupHeader = head;
    this.popupMessage = msg;
    this.popupButton = btn;
    this.popupButton2 = btn2;
    $("#popup2 .popup").addClass("animfadeTopIn");
    },100);
  }
  hidePopup(){
    $(".popup").removeClass("animfadeTopIn");
    $(".popup").addClass("animfadeTopOut");
    setTimeout(()=>{
      this.popupVisible = false;
      this.popup2Visible = false;
      this.popupHeader = " ";
      this.popupMessage = " ";
      this.popupButton = " ";
      this.popupButton2 = " ";
    },400);
    if(this.reload){location.reload()};       
  }

  /**show generic loading indicator**/
  showLoader(){
    $("#loading").fadeIn("fast");
  }
  hideLoader(){
    $("#loading").fadeOut("fast");
  }

  constructor() { }
  
}



