import { Component, OnInit } from '@angular/core';
import { CheckinternetService } from './services/checkinternet.service';
import { ShowpopupService } from './services/showpopup.service';
import { BackbuttonService } from './services/backbutton.service';
import { GlobalsService } from './services/globals.service';
import $ from 'jquery';
 
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit  {
  pipetyp = 'fav';
  title = 'app works!';

  constructor( public checkinter: CheckinternetService,public showpopup: ShowpopupService, public bckbtn: BackbuttonService, public globals: GlobalsService) {}
  

  ngOnInit(){
    this.showpopup.showLoader();
    if(this.checkinter.checkInternet() == true){
        console.log("Internet is available!");
      }
      else{
        console.log("No internet available");
        this.showpopup.showNoInternetPopup()
      }
    // setTimeout(()=>{          
      
    // },3000);    

    document.addEventListener("backbutton", onBackKeyDown.bind(this), false);

    function onBackKeyDown (){
      //console.log("back key pressed.");
      this.bckbtn.onBackButtonPress();
    }
    //this.globals.noInternetTimeout = setTimeout(()=>{this.showpopup.showNoInternetPopup()},10000);    
  }
}



