import { Component, OnInit, Input, NgZone } from '@angular/core';
import { ClosemenuService } from './../services/closemenu.service';
import { GlobalsService } from './../services/globals.service';
import { ShowpopupService } from './../services/showpopup.service';
import $ from 'jquery';

@Component({
  selector: 'app-sidemenu',
  templateUrl: './sidemenu.component.html',
  styleUrls: ['./sidemenu.component.css'],
})
export class SidemenuComponent implements OnInit {
  
  
  @Input() pipetype: string;
  constructor(public clmsrve: ClosemenuService, public globals: GlobalsService,public showpopup: ShowpopupService,private _ngZone: NgZone) {
     
     
   }
    setPipeFilter(pipe){
      this.globals.filterApplied = true;
      this.globals.home = false;
      this._ngZone.run(() => { 
        this.globals.pipetype = pipe;
      })
      this.clmsrve.closeMenu();
      this.showHomeBtn();
      //this.globals.showNavbar = false;
      $("img.lazy").hide();
      setTimeout(()=>{ this.globals.lazyLoadStart();$("img.lazy").show(); },500);
      this.showpopup.showLoader();
      setTimeout(()=>{this.showpopup.hideLoader()},700);
    }
    showHomeBtn(){
      setTimeout(function(){
         $(".homeBtn").addClass("animateShow");
       },100);
    }
    showDownloadedWalls(){
      this.globals.filterApplied = true;
      this.globals.home = false; 
      this.globals.showFeatured = false;
      this.clmsrve.closeMenu();
      this.globals.mainwalls = false;
      this.globals.downloadedwalls = true;
      this.showHomeBtn();
      this.globals.showNavbar = false;
      this.showpopup.showLoader();
      setTimeout(()=>{this.showpopup.hideLoader()},700);
    }

    submitWallPic(){
      this.clmsrve.closeMenu();
      setTimeout(()=>{this.showpopup.showSubmitWallPopup()},300);      
    }
    

  ngOnInit() {
   this.globals.refreshDownAndFavCount();
  }
}



