import { Walllist } from './../shared/wallpaper';
import { Injectable } from '@angular/core';
import  {Http, Response, Headers, RequestOptions} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { ShowpopupService } from './showpopup.service';
import 'rxjs/add/operator/map';
import  'rxjs/add/observable/throw';
import 'rxjs/add/operator/do';
import  'rxjs/add/operator/catch';

@Injectable()
export class WalllistService {

    constructor(private http: Http,public showpopup: ShowpopupService){ }
    getWalllist(){
        var headers = new Headers();
        headers.append('Accept', 'application/json');
        return this.http.get('http://ssgmkharda.in/wallpic/fetchwall.php',{headers: headers}).map((response: Response)=> response.json()).catch(this.handleError);
        //return this.http.get('http://localhost/wallpic-upload/fetchwall.php').map((response: Response)=> response.json()).catch(this.handleError);



        //return this.http.get('http://ssgmkharda.in/wallpic/fetchwall.php').subscribe((response: Response)=> response.json()).catch(this.handleError);

        // return this.http.get('http://ssgmkharda.in/wallpic/fetchwall.php').subscribe(
        //     (response: Response)=> response.json(),err => {
        //     console.log('Something went wrong!');
        //     }
        // );
    }

    updatedDownlist(wallidd){
      var headers = new Headers();
      headers.append('Content-Type','application/json');
      this.http.post('http://ssgmkharda.in/wallpic/updateDownCount.php',
        {wallid : wallidd}, {
        headers: headers
        })
        .subscribe(data => {
            console.log('Request sent to Down php file.');
        }, error => {
            console.log(JSON.stringify(error.json()));
        });
    }

    updateUseCountlist(wallidd){
      var headers = new Headers();
      headers.append('Content-Type','application/json');
      this.http.post('http://ssgmkharda.in/wallpic/updateUseCount.php',
        {wallid : wallidd}, {
        headers: headers
        })
        .subscribe(data => {
            console.log('Request sent to Use count php file.');
        }, error => {
            console.log(JSON.stringify(error.json()));
        });
    }

    addFavlist(wallidd){
      var headers = new Headers();
      headers.append('Content-Type','application/json');
      this.http.post('http://ssgmkharda.in/wallpic/addFavCount.php',
        {wallid : wallidd}, {
        headers: headers
        })
        .subscribe(data => {
            console.log('Request sent to Fav php file.');
        }, error => {
            console.log(JSON.stringify(error.json()));
        });
    }

    removeFavlist(wallidd){
      var headers = new Headers();
      headers.append('Content-Type','application/json');
      this.http.post('http://ssgmkharda.in/wallpic/removeFavCount.php',
        {wallid : wallidd}, {
        headers: headers
        })
        .subscribe(data => {
            console.log('Request sent to Fav php file.');
        }, error => {
            console.log(JSON.stringify(error.json()));
        });
    }



    private handleError(error: Response) {
        let head = "Error!";
        let msg = "Connection error. Please check the internet connection and relaunch the application.";
        let btn = "Ok";
        console.error("Error is:"+error);
        //this.showpopup.showPopup(head,msg,btn);
        //this.showpopup.showNoInternetPopup();
        let message = `Error status code ${error.status} at ${error.url}`;
        return Observable.throw(message);
    }
}





